import React from 'react'
import './index.less'
import {Redirect, Route, Switch} from 'react-router-dom'
import Theme from './pages/Theme/index';
import Face from './pages/Face/index';
import About from './pages/About/index';
import NavFooter from './cpns/NavFooter/index';
import SellerIndex from './pages/Seller/index';
import Admin from './pages/Seller/admin/index';
import Palace from "./pages/Palace";
import moment from 'moment'
import {baseUrl} from "./config";

export default class App extends React.Component {

    componentDidMount() {
        console.log('now',moment().add(2, 'days').unix())

        // 进入登陆
        if (document.location.host === "localhost:3000") {
            console.log('开发者模式');
            localStorage.customer_token = '23_FKQqT5lFSDMsJlZgi0NB1WShBcWfusgQPOwhJRa4LzCH2LTwUmPNBDDnWldVLue6XPzEr3B_f97ziERzAMishQ'
        } else {
            console.log('生产模式');
            // 没有时间戳 -> 未登陆 ->跳转
            // 有时间戳 ->当前小于时间戳时间 -> 设置token
            // 有时间戳 ->当前大于时间戳时间 -> 跳转
            const tt = localStorage.token_time;
            const aj = localStorage.afterJump;
            const jump = () => {
                const u = encodeURIComponent(baseUrl);
                document.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5e44595fadf1d2a2&redirect_uri=${u}%2fwechat_redirect&response_type=code&scope=snsapi_userinfo&state=index.html%23%2fabout`
            };
            const cookie2dic = () => {
                var cookies = document.cookie.split("; ");
                var dic = [];
                for (let i = 0; i < cookies.length; i++) {
                    const e = cookies[i];
                    var key_shit = e.split("=")[0];
                    var val = unescape(e.split("=")[1]);
                    if (key_shit === 'nickName') {
                        val = decodeURI(e.split("=")[1])
                    }
                    dic[key_shit] = val
                }
                console.log('解析后的cookie:', dic);
                return dic
            };
            // 未登录过
            if (tt === undefined && aj === undefined) {
                console.log('第一次登陆');
                localStorage.afterJump = true;
                jump()
            }
            // token超时
            if (tt !== undefined ||localStorage.customer_token ==='undefined') {
                try {
                    if (JSON.parse(tt) < moment().unix()) {
                        console.log('token超时');
                        localStorage.token_time = moment().add(2, 'days').unix();
                        localStorage.afterJump = true;
                        jump()
                    }
                }catch(err){
                    try {
                        console.log('删除掉localstorage')
                        localStorage.removeItem('token_time')
                        localStorage.removeItem('afterJump')
                        localStorage.removeItem('customer_token')
                    }catch (e) {
                        console.log(e)
                    }
                    console.log(err)
                }
            }
            // 已跳转
            if (aj) {
                console.log('已跳转');
                const dt = cookie2dic();
                localStorage.customer_token = dt.token;
                localStorage.token_time = dt.token_time
            }
        }
    }

    render() {
        return (
            <div>
                <div className='main'>
                    <Switch>
                        <Redirect exact from="/" to='/seller' component={SellerIndex}/>
                        <Route path="/seller" component={SellerIndex}/>
                        <Route path="/admin" component={Admin}/>
                        <Route path="/theme" component={Theme}/>
                        <Route path="/palace" component={Palace}/>
                        <Route path="/face" component={Face}/>
                        <Route path="/about" component={About}/>
                    </Switch>
                </div>
                {/* 底部 */}
                <NavFooter/>
            </div>
        )
    }
}
